import React from 'react';
import PropTypes from 'prop-types';
import { Tabs, HorizontalScroll, TabsItem } from '@vkontakte/vkui';

class SortTabs extends React.Component {
  static propTypes = {
    onSelected: PropTypes.func
  };

  static defaultProps = {
    onSelected: null
  };

  state = {
    selectedSort: 'popularity'
  };

  onSelected(sort) {
    this.setState({ selectedSort: sort.by });
    const { onSelected } = this.props;
    if (onSelected) {
      onSelected(sort);
    }
  }

  render() {
    const { selectedSort } = this.state;
    return (
      <Tabs theme="header" type="buttons">
        <HorizontalScroll>
          <TabsItem
            onClick={() => this.onSelected({ by: 'popularity' })}
            selected={selectedSort === 'popularity'}
          >
            
          </TabsItem>
          <TabsItem
            onClick={() => this.onSelected({ by: 'price', ascending: true })}
            selected={selectedSort === 'price'}
          >
            
          </TabsItem>
          <TabsItem
            onClick={() => this.onSelected({ by: 'rating' })}
            selected={selectedSort === 'rating'}
          >
            
          </TabsItem>
          <TabsItem
            onClick={() => this.onSelected({ by: 'experience' })}
            selected={selectedSort === 'experience'}
          >
            
          </TabsItem>
        </HorizontalScroll>
      </Tabs>
    );
  }
}

export default SortTabs;
